<template>
    <div class="detail-wrapper">
        <div class="detail-info">
            <div class="title high-strong">vue常见问题的解决小技巧</div>
            <div class="infos-bar">
                <div class="item"><i class="el-icon-paperclip" />vue</div>
                <div class="item"><i class="el-icon-time" />2019-12-31</div>
                <div class="item"><i class="el-icon-extra-account" />extra_zhao</div>
                <div class="item"><i class="el-icon-extra-good" />88</div>
            </div>
            <div class="detail-context">
                <p class="detail-content">
                    本博客是用vue搭建，虽然简单，但是也涉及vue基本功能和用法，包括路由，组件通信，公共组件的封装等.在这简单博客搭建遇到一些小问题，下面分享下这些问题及解决的小技巧:
                </p>
                <p class="detail-title">
                    1.页面滚动位置的问题.
                </p>
                <p class="detail-content">
                    当页面路由切换的时候，发现页面滚动的位置不变。例如a页面滚动到100px的位置，此时通过路由跳转到b页面，b页面的滚动位置不是在最顶部而是在100px的位置处，我们希望路由跳转到新的组件之后，页面滚动到顶部，
                    而不是保持之前的滚动位置，我们需要修改router配置项,以我自己项目为例,
                    打开router的配置文件，加上scrollBehavior这一段话:
                    <img src="./img/1.jpg" alt="">
                    <pre v-highlight>
                        <code class="lang-javascript">
                           scrollBehavior (to, from, savedPosition) {
                                if (savedPosition) {
                                    // 前进后退
                                    return savedPosition
                                } else {
                                    // 如果不是通过上述行为切换组件，就会让页面回到顶部
                                    return {x: 0, y: 0}
                                }
                            }
                        </code>
                    </pre>
                </p>
            </div>
        </div>
        <comment />
    </div>
</template>

<script type="text/ecmascript-6">
    import comment from './comment.vue'
    export default {
        name: '',
        data(){
            return {

            }
        },
        created() {

        },
        mounted() {

        },
        methods: {},
        components: {
            comment
        }
    }
</script>

<style scoped lang="less">
@import "./detail.less";
</style>



