<template>
    <div class="comment-wrapper">
        <p class="detail-title high-strong"> <i class="el-icon-extra-imagetext" /> 兄台，请指点一二~</p>
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
            <el-form-item label="">
                <el-input clearable prefix-icon="el-icon-extra-bussinesscard" size="small" v-model="formInline.name" placeholder="* 怎么称呼" />
            </el-form-item>
            <el-form-item label="">
                <el-input clearable prefix-icon="el-icon-extra-survey1" size="small" v-model="formInline.comment" placeholder="* 兄台，有何高见" />
            </el-form-item>
            <el-form-item label="">
                <el-input clearable prefix-icon="el-icon-extra-gerenzhongxin" size="small" v-model="formInline.contact" placeholder="* 您的联系方式或主页" />
            </el-form-item>
            <el-form-item>
                <el-button icon="el-icon-extra-trust" size="small" type="primary">提交</el-button>
            </el-form-item>
        </el-form>
        <p class="detail-title high-strong">已有5条评论</p>
        <v-comment />
        <v-comment />
        <v-comment />
    </div>
</template>

<script type="text/ecmascript-6">

    export default {
        name: '',
        data(){
            return {
                formInline: {
                    name: '',
                    comment: '',
                    contact:""
                }
            }
        },
        created() {

        },
        mounted() {

        },
        methods: {},
        components: {}
    }
</script>

<style scoped lang="less">
    .comment-wrapper{
        margin-top: 20px;
        background-color: #fff;
        padding:15px 20px 40px 20px;
        .detail-title{
            margin-bottom:15px;
        }
    }
</style>



